<template>
  <el-container>
    <el-header>警情识别系统</el-header>
    <el-main>
      <div class="left">
        <el-card>
          <div slot="header" class="clearfix">
            <span>警情信息</span>
          </div>
          <el-form :model="formData" :disabled="true"
                   label-width="80px"
                   class="demo-form-inline">
            <el-form-item label="重要类型">
              <el-input class="width-20" v-model="formData.title" placeholder="标题">
              </el-input>
            </el-form-item>
            <el-form-item label="警情摘要">
              <el-input type="textarea" class="width-80" :rows="3"
                        v-model="formData.description" placeholder="警情摘要">
              </el-input>
            </el-form-item>
            <el-form-item label="反馈信息">
              <el-input type="textarea" class="width-80" :rows="3"
                        v-model="formData.feedback" placeholder="反馈信息">
              </el-input>
            </el-form-item>
          </el-form>
          <div style="text-align: center">
            <el-button type="primary" icon="el-icon-back" @click="onPrev">
              上一条
            </el-button>
            <el-button type="primary" icon="el-icon-right" @click="onNext">
              下一条
            </el-button>
          </div>
        </el-card>
      </div>
      <div class="middle">
        <el-button type="primary" icon="el-icon-magic-stick" @click="onSearch">
          智能分析
        </el-button>
      </div>
      <div class="right">
        <el-card class="box-card">
          <div slot="header" class="clearfix">
            <span>分析结果</span>
          </div>
          <el-row :gutter="12" v-loading="loading">
            <el-tree :data="data" :props="treeProps" :default-expand-all="true"
                     @node-click="handleNodeClick"></el-tree>
          </el-row>
        </el-card>
      </div>
    </el-main>
  </el-container>
</template>

<script>
import policeaffairs from '@/api/policeaffairs';

const defaultData = {
  informations: [
    {
      id: 4379,
      important_type: '盗销自行车',
      lines: ['4379', '', '', '', '', '张三报在中关村西地铁南口的北边的公交车站发现电动车被盗', '民警赶到现场，在中关村地铁北口找到电动车', '', '', '海淀分局'],
    },
  ],
  topN: 5,
};

export default {
  data() {
    return {
      defaultData,
      loading: false,
      formData: {
        id: '',
        description: '警情摘要',
        feedback: '反馈信息',
      },
      dataList: [],
      data: [{
        label: '一级 1',
        children: [{
          label: '二级 1-1',
          children: [{
            label: '三级 1-1-1',
          }],
        }],
      }, {
        label: '一级 2',
        children: [{
          label: '二级 2-1',
          children: [{
            label: '三级 2-1-1',
          }],
        }, {
          label: '二级 2-2',
          children: [{
            label: '三级 2-2-1',
          }],
        }],
      }, {
        label: '一级 3',
        children: [{
          label: '二级 3-1',
          children: [{
            label: '三级 3-1-1',
          }],
        }, {
          label: '二级 3-2',
          children: [{
            label: '三级 3-2-1',
          }],
        }],
      }],
      treeProps: {
        children: 'children',
        label: 'label',
      },
    };
  },
  methods: {
    onSearch() {
      this.formData.id = this.getRandom();
      this.loading = true;
      policeaffairs.search({ informations: [this.formData], topN: 1 })
        .then((res) => {
          this.loading = false;
          if (res && res.data && res.data.length) {
            const [identify] = res.data;
            const { content: { category } } = identify;
            this.dataList = category;
          }
        }).catch((err) => {
          this.loading = false;
          console.log(err);
        });
    },
    onPrev() {
    },
    onNext() {
    },
    getRandom(start = 0, end = 100, fixed = 0) {
      const differ = end - start;
      const random = Math.random();
      return (start + differ * random).toFixed(fixed);
    },
    handleNodeClick(data) {
      console.log(data);
    },
  },
};
</script>

